.search {
  width: 220px;
  text-align: right;
}
.search input {
  width: 150px;
  border: none;
  border-bottom: 2px solid black;
  outline: none;
  transition: 0.25s;
}

.inputAnimated {
  position: relative;
  top: -10px;
  width: 10px !important;
}
.inputAnimated:focus {
  top: 0px;
  width: 150px !important;
}

.searchText {
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
  cursor: pointer;
  font-size: 13pt;
  margin-left: 5px;
}/*# sourceMappingURL=index.css.map */