.centerContent {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 20px;
}

.content {
  width: 80%;
}

.tools {
  padding: 0px 10px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.showTypes {
  width: 220px;
  display: flex;
  gap: 4px;
}
.showTypes div {
  border: 1px solid black;
  cursor: pointer;
  height: 32px;
}
.showTypes div svg {
  pointer-events: none;
}

.data {
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
}

body::-webkit-scrollbar {
  width: 6px;
  overflow: overlay;
}

body::-webkit-scrollbar-track {
  background: transparent;
}

body::-webkit-scrollbar-thumb {
  background-color: #000000;
}/*# sourceMappingURL=index.css.map */