.label {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 35px;
  background-color: rgb(163, 177, 218);
}
.label div {
  margin-top: -3px;
  flex: 100;
  padding-left: 30px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}
.label div:nth-child(4) {
  flex: 150;
}
.label div:nth-child(5) {
  flex: 50;
}

.arrowIcon {
  display: inline-block;
  opacity: 0;
  position: relative;
  top: 2px;
  left: 4px;
  transition: 0.05s;
}/*# sourceMappingURL=index.css.map */