.row {
  background-color: rgb(235, 235, 235);
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 35px;
}
.row div {
  flex: 100;
  padding-left: 15px;
}
.row:hover {
  background-color: rgb(225, 225, 225);
}
.row:nth-child(2n) {
  background-color: rgb(200, 200, 200);
}
.row:nth-child(2n):hover {
  background-color: rgb(190, 190, 190);
}

.rowSelected {
  background-color: rgb(189, 211, 240) !important;
}
.rowSelected:nth-child(2n) {
  background-color: rgb(154, 176, 205) !important;
}/*# sourceMappingURL=index.css.map */